function Copyright() {
	return (
		<Typography variant='body2' color='textSecondary' align='center'>
			<br></br>
			{'Copyright Â© '}
			<Link color='inherit' href='https://github.com/vizbyarcher/'>
				VizbyArcher
			</Link>{' '}
			{new Date().getFullYear()}
			{'.'}
		</Typography>
	)
}

const fetchURL =
	'https://raw.githubusercontent.com/vizbyarcher/provaccin/master/data/vaccinari.json'
const getItems = () => fetch(fetchURL).then(res => res.json())

const propertyFirst = 'county'
const propertySecond = 'city'
const propertyThird = 'name'
const propertyFourth = 'date'
const propertyFifth = 'available'
const propertySixth = 'scheduled'
const propertySeventh = 'address'
const propertyEigth = 'id'

let sortBy = [
	{
		prop: propertyFifth,
		direction: -1,
	},
	{
		prop: propertySixth,
		direction: 1,
	},
]
// data.sort((a, b) => a.city.localeCompare(b.city) || b.price - a.price);
const changeFirst = data => {
	if (data) {
		// console.log(data)
		const unique = [
			...new Set(
				data
					.sort((a, b) => b[propertyFirst] - a[propertyFirst])
					.map(item => item[propertyFirst])
			),
		]
		// console.log(unique)
		return unique.sort()
	} else {
		// console.log('No data for First combo...')
		return []
	}
}

const changeSecond = (data, valueFirst) => {
	if (valueFirst) {
		// console.log('Changing Second combo...')
		const filteredFirst = data.filter(
			item => item[propertyFirst] === valueFirst
		)
		const unique = [...new Set(filteredFirst.map(item => item[propertySecond]))]
		// console.log(unique)
		return unique.sort()
	} else {
		return []
	}
}

const changeThird = (data, valueFirst, valueSecond) => {
	if (valueFirst) {
		// console.log('Changing Third combo...')
		const filteredFirst = data.filter(
			item => item[propertyFirst] === valueFirst
		)
		if (valueSecond) {
			const filteredSecond = filteredFirst.filter(
				item => item[propertySecond] === valueSecond
			)
			const unique = [
				...new Set(filteredSecond.map(item => item[propertyThird])),
			]
			// console.log('There is second value')
			return unique.sort()
		} else {
			const unique = [
				...new Set(filteredFirst.map(item => item[propertyThird])),
			]
			console.log(unique)
			console.log(unique.sort())
			return unique.sort()
		}
	} else {
		return []
	}
}

const ComboBoxItems = () => {
	// const handleOnChange = ({ target }) => console.log(target.value)
	const [valueFirst, setValueFirst] = useState('Bucuresti')
	const [valueSecond, setValueSecond] = useState(null)
	const [valueThird, setValueThird] = useState(null)
	const [items, setItems] = useState([])
	const [itemsFirst, setItemsFirst] = useState([])
	const [itemsSecond, setItemsSecond] = useState([])
	const [itemsThird, setItemsThird] = useState([])

	const [updated, setUpdated] = useState(null)
	// data.sort((a, b) => b[propertyFifth] - a[propertyFifth])
	// data.sort((a, b) => a.city.localeCompare(b.city) || b.price - a.price);
	useEffect(() => {
		getItems().then(data => {
			data.sort(
				(a, b) =>
					b[propertyFifth] - a[propertyFifth] ||
					a[propertySixth] - b[propertySixth]
			)
			setItems(data)
			setUpdated(data.find(x => x.id === 0)[propertyFourth])
		})
	}, [])

	useEffect(() => {
		if (items) {
			setItemsFirst(changeFirst(items))
		} else {
			console.log('No data for itemsFirst')
		}
	}, [items])

	useEffect(() => {
		if (valueFirst) {
			setItemsSecond(changeSecond(items, valueFirst))
		} else {
			setItemsSecond([])
		}
	}, [items, valueFirst])

	useEffect(() => {
		if (items.length > 0) {
			if (valueFirst) {
				setItemsThird(changeThird(items, valueFirst, valueSecond))
				if (!valueSecond && valueThird) {
					setValueSecond(
						items.find(x => x[propertyThird] === valueThird)[propertySecond]
					)
				}
			} else {
				setItemsThird([])
			}
		}
	}, [items, valueFirst, valueSecond, valueThird])

	const DenseTable = props => {
		const classes = useStyles()
		const items = props.props.items
		const valueFirst = props.props.valueFirst
		const valueSecond = props.props.valueSecond
		const valueThird = props.props.valueThird

		let filtered = items
		if (valueThird) {
			filtered = items
				.filter(item => item[propertyFirst] === valueFirst)
				.filter(item => item[propertySecond] === valueSecond)
				.filter(item => item[propertyThird] === valueThird)
		} else if (valueSecond) {
			filtered = items
				.filter(item => item[propertyFirst] === valueFirst)
				.filter(item => item[propertySecond] === valueSecond)
		} else if (valueFirst) {
			filtered = items.filter(item => item[propertyFirst] === valueFirst)
		}

		return (
			<TableContainer component={Paper}>
				{filtered && (
					<div>
						<small>
							<i>{filtered.length} inregistrari</i>
						</small>
					</div>
				)}
				<Table
					className={classes.table}
					size='small'
					aria-label='a dense table'>
					<TableHead>
						<TableRow>
							<TableCell>Centrul</TableCell>
							<TableCell align='right'>Libere</TableCell>
							<TableCell align='right'>In&nbsp;asteptare</TableCell>
							<TableCell align='right'>Localitatea</TableCell>
							<TableCell align='right'>Judetul</TableCell>
							<TableCell align='right'>Adresa</TableCell>
							{/* <TableCell align='right'>Actualizat&nbsp;la</TableCell> */}
						</TableRow>
					</TableHead>
					<TableBody>
						{filtered.map(item => (
							<TableRow key={item[propertyEigth]}>
								<TableCell component='th' scope='row'>
									{item[propertyThird]}
								</TableCell>
								<TableCell align='right'>{item[propertyFifth]}</TableCell>
								<TableCell align='right'>{item[propertySixth]}</TableCell>
								<TableCell align='right'>{item[propertySecond]}</TableCell>
								<TableCell align='right'>{item[propertyFirst]}</TableCell>
								<TableCell align='right'>{item[propertySeventh]}</TableCell>
								{/* <TableCell align='right'>{item[propertyFourth]}</TableCell> */}
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
		)
	}

	return (
		<div className='combo-county'>
			{items && (
				<Autocomplete
					id='combo-box-first'
					options={itemsFirst}
					getOptionLabel={option => option}
					value={valueFirst}
					style={{ width: 380 }}
					onChange={(event, value) => {
						// console.log(`first: ${value}`)
						if (value) {
							// console.log('changing first value', value)
							setValueFirst(value)
							setValueSecond(null)
							setValueThird(null)
						} else {
							// console.log('no first value...')
							setValueFirst(null)
							setValueSecond(null)
							setValueThird(null)
						}
					}}
					renderInput={params => (
						<TextField
							{...params}
							label='Judetul:'
							variant='outlined'
							fullWidth
						/>
					)}
				/>
			)}
			<br></br>
			{items && (
				<Autocomplete
					id='combo-box-second'
					options={itemsSecond}
					getOptionLabel={option => option}
					value={valueSecond}
					style={{ width: 380 }}
					onChange={(event, value) => {
						// console.log(`second: ${value}`)
						if (value) {
							// console.log('changing second value', value)
							setValueSecond(value)
							setValueThird(null)
						} else {
							// console.log('no second value...')
							setValueSecond(null)
							setValueThird(null)
						}
					}}
					renderInput={params => (
						<TextField
							{...params}
							label='Localitatea/sectorul:'
							variant='outlined'
							fullWidth
						/>
					)}
				/>
			)}
			<br></br>
			{items && (
				<Autocomplete
					id='combo-box-second'
					options={itemsThird}
					getOptionLabel={option => option}
					value={valueThird}
					style={{ width: 380 }}
					onChange={(event, value) => {
						// console.log(`third: ${value}`)
						if (value) {
							// console.log('changing third value', value)
							setValueThird(value)
						} else {
							// console.log('no third value...')
							setValueThird(null)
						}
					}}
					renderInput={params => (
						<TextField
							{...params}
							label='Centrul:'
							variant='outlined'
							fullWidth
						/>
					)}
				/>
			)}
			{/* {valueFirst && <div>{`first: ${valueFirst}`}</div>} */}
			{/* {valueSecond && <div>{`second: ${valueSecond}`}</div>} */}
			{/* {valueThird && <div>{`third: ${valueThird}`}</div>} */}
			<br></br>
			{updated && (
				<p>
					<small>Date actualizate la: {updated}</small>
				</p>
			)}
			{updated && (
				<DenseTable props={{ items, valueFirst, valueSecond, valueThird }} />
			)}
		</div>
	)
}

export default function App() {
	//  https://stackoverflow.com/questions/36878255/allow-access-control-allow-origin-header-using-html5-fetch-api

	// console.log(data)

	return (
		<Container maxWidth='sm'>
			<Box my={4}>
				<Typography variant='h5' component='h1' gutterBottom>
					PROgramare VACCIN COVID-19
				</Typography>
				<Body link={'www.google.com'} />

				<ComboBoxItems />

				<Copyright />
			</Box>
		</Container>
	)
}
